import React, { useState } from 'react';

import { TabBarItem } from './TabBarItem';
import { ApartmentsForm } from 'components/ApartmentsForm'; 

import styles from './TabBar.module.scss';

export const TabBar = () => {
  const [tab, setTab] = useState(1);

  return (
    <section className={styles.tabBar}>
      <TabBarItem isActive={tab === 1 ? true : false} nav={'  '} tabHandler={() => setTab(1)}>
        <ApartmentsForm />
      </TabBarItem>
      <TabBarItem isActive={tab === 2 ? true : false} nav={'  '} tabHandler={() => setTab(2)}>
        <div>     </div>
      </TabBarItem>
      <TabBarItem isActive={tab === 3 ? true : false} nav={'  '} tabHandler={() => setTab(3)}>
        <div>     </div>
      </TabBarItem>
      <TabBarItem
        isActive={tab === 4 ? true : false}
        nav={' '}
        tabHandler={() => setTab(4)}
        lastchild={true}
      >
        <div>    </div>
      </TabBarItem>
    </section>
  );
};
